import React from 'react'
import Link from 'gatsby-link'

const NotFoundPage = ({data}) => (
  <article>
    <header>
      <h1>Page Not Found</h1>
    </header>
    <div className="body">
      <p>Are you lost? The page your looking for doesn't exists.</p>
      <br/>
      <h2>I suggest you read my other posts below.</h2>
      <ul className="suggestion-post-list">
        {
          data.allMarkdownRemark.edges.map(post => (
            <li key={post.node.id}>
              <Link to={post.node.frontmatter.path} className="post-item">
                <span className="title">{post.node.frontmatter.title}</span>
                <span className="meta">{post.node.frontmatter.date} | By: Mark Anthony Uy</span>
              </Link> 
            </li>
          ))
        }
      </ul>
    </div>
  </article>
)

export const pageQuery = graphql`
  query NotFoundQuery {
    allMarkdownRemark(limit: 5) {
      edges {
        node {
          id
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            path
            title
          }
        }
      }
    }
  }
`

export default NotFoundPage



